.Header {
    background-image: url("../../images/header.png");;
    background-position: top;
    width: 100%;
    height: 960px;

}

.pictures {
    width: 100%;
    height: 960px;
}

.overlay {
    background-color: rgb(42 108 234 / 81%);
    padding: 20px;
    height: 100%;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: center;
}

h1 {
    font-size: 52px;
    margin-bottom: 1rem;
    color: rgba(255, 255, 255, 1);
    text-align: center;
}

p {
    font-size: 18px;
    margin-bottom: 2rem;
    color: rgba(255, 255, 255, 1);
    text-align: center;

}

.infoBlock {
    width: 748px;
    align-items: center;
    display: grid;
}

.button {
    background-color: rgba(236, 239, 242, 1);
    color: #000;
    border: none;
    padding: 10px 20px;
    font-size: 1rem;
    cursor: pointer;
    border-radius: 5px;
    height: 64px;
    width: 251px;
    margin: 60px auto;
    font-family: "Graphik Light", serif;
}

button:hover {
    background-color: #ddd;
}

@media screen and (min-width: 768px) and (max-width: 1200px) {
    h1 {
        font-size: 32px;
    }

    p {
        font-size: 16px;

    }
    .infoBlock {
        width: 476px;
    }

}

@media screen and (min-width: 375px) and (max-width: 767px) {
    h1 {
        font-size: 24px;
    }

    p {
        font-size: 16px;

    }
    .infoBlock {
        width: 288px;
    }

}

@media screen and (min-width: 320px) and (max-width: 374px) {
    h1 {
        font-size: 24px;
    }

    p {
        font-size: 16px;

    }
    .infoBlock {
        width: 288px;
    }

}
