.Questions {

}

.faqContainer {
    max-width: 1008px;
    width: 90%;
    margin: 0 auto;
    padding: 20px;
}

h2 {
    display: flex;
    justify-content: center;
    margin-bottom: 2rem;
    margin-top: 2rem;

}

.faqContainer h1 {
    text-align: center;
    margin-bottom: 20px;
}

.faqItem {
    border-bottom: 1px solid #ddd;
    padding: 0 0 30px 0;
    margin: 40px 0 40px 0;
}

.faqQuestion {
    display: flex;
    justify-content: space-between;
    cursor: pointer;
    //font-weight: bold;
    font-size: 18px;
}

.faqQuestion:hover {
    color: #2A6CEA;
    font-size: 18px;

}

.faqAnswer  {
    margin-top: 10px;
    font-size: 18px;
    font-family: "Graphik Light", serif;
    color: rgba(94, 100, 106, 1);;

}

.faqToggle {
    font-size: 24px;
}

