.navbar {
    display: flex;
    position: absolute;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    max-width: 1920px;
    width: 100%;
    color: #fff;
    box-sizing: border-box;
}

.navbarLogo {
    display: flex;
    align-items: center;

    img {
        margin-right: 0.5rem;
    }

    span {
        font-size: 1.5rem;
        font-weight: bold;
    }
}

.navbarLinks {
    display: flex;

    .link {
        margin-left: 1.5rem;
        color: #fff;
        text-decoration: none;
        font-size: 1rem;

        &:hover {
            text-decoration: underline;
        }


    }

    @media (max-width: 768px) {
        display: none;
    }
}

.menuIcon {
    display: none;
    cursor: pointer;

    img {
        width: 24px;
        height: 24px;
    }

    @media (max-width: 768px) {
        display: block;
    }
}

.mobileMenu {
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    box-sizing: border-box;
    height: 100vh;
    background-color: #ffffff;
    color: #000;
    display: flex;
    flex-direction: column;
    padding: 1rem;
    box-shadow: -2px 0 5px rgba(0, 0, 0, 0.5);
}

.header {
    width: 100%;
    height: 80px;
    position: relative;
    border-bottom: 1px solid #ddd;

}

.boxLink {
    position: relative;
    border-bottom: 1px solid #ddd;
    height: 50px;
    margin-top: 1.5rem;

}

.angele {
    position: absolute;
    top: 0;
    right: 0;
}

.lineLeft {
    position: absolute;
    top: 0;
    right: 0;
}

.lineRight {
    position: absolute;
    top: 0;
    right: 0;
}

.mobileMenu a {
    margin-bottom: 1.5rem;
    color: rgb(94, 100, 106);
    text-decoration: none;
    font-size: 1.2rem;
    font-family: "Graphik Light", serif;
}

.mobileMenu a:hover {
    text-decoration: underline;
}

.closeIcon {
    font-size: 1.5rem;
    font-weight: bold;
    cursor: pointer;
    align-self: flex-end;
    margin-bottom: 2rem;
}


